<script>
macgyver.register('mgIcon', {
	title: 'Icon',
	icon: 'far fa-flag',
	category: 'Simple Inputs',
	preferId: true,
	config: {
		iconFallback: {type: 'mgIcon', default: 'far fa-info', help: 'The icon to use if non is selected'},
		required: {type: 'mgToggle', default: false},
		interface: {type: 'mgChoiceButtons', default: 'modal', enum: ['modal', 'dropdown']},
		iconFeed: {type: 'mgText', default: '/api/webfonts/fa.json', advanced: true, help: 'The data source to import icon information', relative: true},
		class: {type: 'mgText', default: 'btn btn-light btn-circle', advanced: true},
		classActive: {type: 'mgText', advanced: true},
		classInactive: {type: 'mgText', advanced: true},
	},
	format: true,
});

export default Vue.component('mgIcon', {
	inject: ['$mgForm'],
	data() { return {
		data: undefined,
	}},
	props: {
		config: Object,
	},
	created() {
		this.$mgForm.inject(this);
		this.$on('mgValidate', reply => {
			if (this.$props.config.required && !this.data) return reply(`${this.$props.config.title} is required`);
		});
	},
	methods: {
		selectIcon() {
			Promise.resolve()
				.then(()=> this.$macgyver.notify.loading(this._uid, true))
				.then(()=> this.$http.get(this.$props.config.iconFeed))
				.then(res => { this.$macgyver.notify.loading(this._uid, false); return res })
				.then(res => this.$macgyver.$prompt.macgyver({
					title: 'Select icon',
					buttons: [], // We're capturing the first click so we don't need confirm buttons
					macgyver: [
						{
							id: 'class',
							type: 'mgChoiceButtons',
							showTitle: false,
							classWrapper: '',
							enum: res.data.map(icon => ({
								id: icon.class,
								class: `btn btn-icon-fixed ${icon.class} fa-fw`,
								classActive: `btn btn-primary btn-icon-fixed ${icon.class} fa-fw`,
							})),
						},
					],
					onShow: ()=> {
						// Bind to the mg-form element, detect the first change and close the dialog
						this.$macgyver.$prompt.settings.macgyverForm.$on('mgChange', ()=> setTimeout(()=> { // Timeout not really needed but it lets the button highlight before we close
							this.$macgyver.$prompt.close(true, this.$macgyver.$prompt.settings.value);
						}, 100));
					},
				}))
				.then(form => this.data = form.class)
		},
	},
});
</script>

<template>
	<div class="mg-icon">
		<a
			v-if="$props.config.interface == 'modal'"
			@click="selectIcon()"
			class="btn btn-light btn-icon-fixed"
			:class="data ? [data, $props.config.classActive || $props.config.class] : [$props.config.iconFallback, $props.config.classInactive || $props.config.class]"
		/>
		<mg-choice-dropdown
			v-else-if="$props.config.interface == 'dropdown'"
			:form="$props.form"
			:data="data"
			:config="{
				$dataPath: $props.config.$dataPath,
				enumSource: 'url',
				enumUrl: {
					url: $props.config.iconFeed,
					type: 'array',
					mappings: {
						id: {required: true, from: 'class'},
						title: {required: true, from: 'id'},
						icon: {required: true, from: 'class'},
					},
				},
				default: $props.config.default,
				required: $props.config.required,
			}"
		/>
		<mg-error
			v-else
			:config="{errorText: 'Unknown mgIcon interface'}"
		/>
	</div>
</template>

<style>
.btn.btn-icon-fixed {
	box-shadow: none;
	width: 32px;
	height: 32px;
	padding: 7px 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
</style>
